.conteiner-teacher {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    font-family: "Amatic SC", cursive;
    font-weight: bold;
    font-size: 25px;
    background-color: #eee
  }
  .img-teacher {
    border-radius: 50%;
    width: 80%;
    padding: 15px;
  }
  .miembro {
    width: calc(25% - 20px);
    margin: 10px;
    padding: 15px;
    border: 1px solid #ccc;
    text-align: center;
  }
  
  .miembro img {
    max-width: 100%;
  }
  
  .miembro h3 {
    margin-top: 10px;
  }
  
  .miembro button {
    margin-top: 10px;
    background-color: #007bff;
    color: white;
    border: none;
    padding: 5px 10px;
    cursor: pointer;
  }
  
  .miembro button:hover {
    background-color: #0056b3;
  }
  
  
  /* Agrega estos estilos al archivo Teacher.css */
  .modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex; 
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  
  .modal-testimonial {
    background-color: white !important;
    max-width:50%;
    height:90%;
    margin-top:2em;
    left:15em !important;
    padding:10px !important;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.3);
    position: absolute;
    left:25px;
  }
  
  .close {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 30px;
    width: 5%;
    cursor: pointer;
    border-radius:50%;
    color: #fc2403 ;
    border: 1px solid #fc2403 ;
  }
  .close:hover{
    background:#fc2403 ;
    color: #fff;
  }
  
  .modal-testimonial img {
    max-width: 20%;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  
  .modal-testimonial h3 {
    margin: 0;
    padding: 0;
    font-size: 24px;
    color: #fc2403 ;

  }
  
  .parrafo-info {
    font-size: 26px;
    color: #fc2403  !important;
    margin-bottom: 20px;
  }
  
  .btn-close {
    background-color: #007bff !important;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .btn-close:hover {
    background-color: #0056b3 !important;
  }
  