/* Formulario.css */
.modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Fondo semitransparente */
  z-index: 1000;
}

.modal-header-close {
  border-radius: 8px;
  position: absolute;
  z-index: 105;
  padding: 20px;
  width: 400px;
  display:flex;
  justify-content: flex-end;
  margin: 0 auto;
}
/* Estilos para el botón de cierre */
.modal-dismiss {
  right: 30px;
  top:-10px;
  padding: 5px;
  background-color: #e84b2c !important;
  border: none;
  cursor: pointer;
  font-size: 18px;
  line-height: 1;
  position: absolute;
}
.modal-dismiss:hover {
  background-color: #bd3a20 !important;
  box-shadow: 0 0 10px red !important;
}

.form-img-sentidos {
  width: 50%;
  border-radius: 15px;
}
.modal-content {
  background-color: white;
  border-radius: 8px;
  padding: 20px;
  text-align: center;
  max-width: 400px;
  height: 500px;
  margin: 0 auto;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.modal h2 {
  margin-top: 0.1em;
  color: red;
}

.modal p {
  margin: 0.3em 0;
}

.modal button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 4px;
  margin-top: 10px;
}

.modal button:hover {
  background-color: #0056b3;
}

.content {
  /* Estilos para el contenido de la página, si lo tienes */
}

/* Resto de tus estilos aquí */
.register-link {
  text-decoration: none;
  background-color: #3f9dd4;
  color: #fff;
  font-weight: bold;
  border-radius: 5px;
  padding: 15px;
  margin-bottom: 20px;
}
