/* Estilos para el formulario */
.login {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ccc;
  background-color: #f9f9f9;
  border-radius: 5px;
}

.login h1 {
  text-align: center;
  margin-bottom: 20px;
}

.login input[type="text"],
.login input[type="password"] {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 3px;
  text-align: center;
}

.login button {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

.login button:hover {
  background-color: #0056b3;
}

/* Estilos adicionales */
.additional-content {
  margin-top: 30px;
}

.additional-content img {
  max-width: 100%;
}

.additional-content hr {
  margin-top: 20px;
}

/* Media query para pantallas de hasta 768px */
@media (max-width: 768px) {
  .login {
    max-width: 100%;
    padding: 10px;
  }

  .login input[type="text"],
  .login input[type="password"] {
    padding: 8px;
  }

  .login button {
    padding: 8px;
  }
}
