/* Chatbot.css */

.chatbot {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background-color 0.3s ease;
  z-index: 1000;
}

.div-respuesta-bot{
  background:#eee;
}
.div-respuesta-bot ul{
  margin-top:10px;
  color:#333 !important;
  font-weight:bold;
}
.div-respuesta-bot li{
  color:#fff !important;
  text-align:center;
  background:#007bff;
  padding:5px;
  margin-top:30px;
  margin-bottom:30px;
  border-radius:15px;
  font-weight:bold;
}
.div-respuesta-bot a{
  color:#fff !important;
  padding:5px;
  margin-top:30px;
  margin-bottom:30px;
  border-radius:15px;
  font-weight:bold;
}

.chatbot.active {
  width: 400px;
  height: 400px;
  border-radius: 10px;
}

.chatbot-toggle img {
  width: 80px;
  height: 80px;
  background: transparent;
  color: transparent;
  z-index: -2;
  border-radius: 50%;
  animation: vibrating 2s infinite alternate;
  border: 8px solid red;
}

@keyframes vibrating {
  from {
    transform: translateX(-5px);
  }
  to {
    transform: translateX(5px);
  }
}

.chatbot-toggle::before,
.chatbot-toggle::after {
  content: "";
  font-size: 25px;
  position: absolute;
  color: #000;
  font-weight: bold;
}

.chatbot.active .chatbot-toggle::before,
.chatbot.active .chatbot-toggle::after {
  color: transparent; /* Hace que el contenido sea transparente en el estado activo */
}

.chatbot-toggle::before {
  top: -120px;
  left: -30%;
  transform: translateX(-60%);
  animation: notes-floating 6s infinite;
}

.chatbot-toggle::after {
  top: -70px;
  right: 50%;
  transform: translateX(50%);
  animation: notes-floating 6s infinite 0.5s;
}

@keyframes notes-floating {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(-30px);
  }
}
/* Chatbot.css */

.chatbot-toggle::before {
  content: "Hola!";
}

.chatbot-toggle::after {
  content: "Soy Flappy";
  /* ... otros estilos ... */
}

.chatbot-container {
  width: 100%;
  height: 100%;
  background-color: #f7f7f7;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  border: 2px solid #000;
  padding: 5px;
}

.chatbot-header {
  background-color: #007bff;
  color: #fff;
  padding: 10px;
  text-align: center;
  font-weight: bold;
  border-radius: 10px;
}

.chatbot-messages {
  flex-grow: 1;
  padding: 10px;
  overflow-y: scroll;
}

.message {
  margin: 10px 0;
  padding: 8px 12px;
  border-radius: 8px;
}



.user-message {
  background-color: #ccc;
  color: #000;
  align-self: flex-end;
}

.bot-message {
  background-color: #f1f1f1;
  color: #333;
  align-self: flex-start;
}

.chatbot-input {
  display: flex;
  align-items: center;
  padding: 10px;
  background-color: #fff;
  border-top: 1px solid #ccc;
}

.chatbot-input input {
  flex-grow: 1;
  border: none;
  padding: 8px;
  border-radius: 4px;
  margin-right: 8px;
  width: 100%;
}

.chatbot-input button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
}

/* Chatbot.css */
.wave-animation {
  animation: wave 1.5s infinite;
}

@keyframes wave {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.letter-wave {
  display: inline-block;
  animation: wave 1.5s infinite;
}


